* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.product {
  padding-top: 40px;
  width: 100vw;
  height: 100vh;
  background: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.buttons {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  height: 100%;
  width: 120px;
  color: white;
  font-family: Roboto;
  font-size: 23px;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button:hover {
  color: rgb(245, 247, 142);
  cursor: pointer;
}

.product-cc {
  width: 100%;
  height: 75%;
  position: relative;
  margin-top: 30px;
}

.msk {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cc {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
}

.produc {
  width: 100%;
  height: 100%;
  margin-right: 20px;
  position: relative;
  opacity: 0;
  transition-duration: 1s ease;
}

.img {
  width: 100%;
  height: 100%;
}

.c-c {
  width: 100%;
  height: 100%;
  position: relative;
}

.p-name {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 60px;
  font-weight: bold;
  font-family: Montserrat;
  letter-spacing: 5px;
  color: rgb(252, 249, 86);
  width: 100%;
  height: 120px;
  background: linear-gradient(to left, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.2));
  display: flex;
  align-items: center;
  justify-content: center;
}

.img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  display: flex;
  margin-top: 20px;
  align-items: center;
  height: 60px;
  width: 100%;
  justify-content: space-between;
  padding: 0px 10px;
}

.categ {
  color: white;
  font-size: 18px;
  font-family: Lobster;
}
.name {
  color: white;
  font-size: 20px;
  font-family: Montserrat;
  letter-spacing: 2px;
}

.desc {
  width: 90%;
  min-height: 80px;
  margin-top: 20px;
}

.desc p {
  color: white;
  font-family: Roboto;
  word-wrap: break-word;
  letter-spacing: 1px;
  font-size: 17px;
}

.left,
.right {
  width: 200px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left i,
.right i {
  font-size: 3rem;
  color: rgba(48, 45, 45, 0.8);
}

.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

.prod-left {
  transform: translateY(100%);
}

.prod-right {
  transform: translateY(-100%);
}

.p-button {
  width: 300px;
  height: 80px;
  position: absolute;
  z-index: 50;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  top: 30px;
  right: 30px;
  border: 2px solid rgb(226, 226, 102);
  border-radius: 8px;
}

.p-pButton {
  font-family: Lobster;
  font-size: 30px;
  color: rgb(226, 226, 102);
  text-decoration: none;
  text-align: center;
  letter-spacing: 2px;
}

@media screen and (max-width: 800px) {
  .img img {
    object-fit: cover;
  }
  .p-button {
    width: 200px;
    height: 60px;
  }
  .p-pButton {
    font-size: 20px;
  }
  .p-name {
    font-size: 34px;
  }
}
