* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 40%;
  height: 100%;
}

.navbar-nav {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.nav-item {
  width: 25%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav-link {
  display: flex;
  width: 100%;
  height: 90%;
  text-align: center;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: white;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2px;
}

.nav-link:hover {
  color: rgb(73, 152, 241);
}

.act {
  background: red;
  width: 4px;
  height: 20px;
  display: flex;
  justify-self: flex-end;
}

.act1 {
  background: red;
  width: 100px;
  height: 4px;
  display: flex;
  justify-self: flex-end;
}

.inactive {
  background: transparent;
}

.nav-nav1 .nav-link {
  color: black;
}

.nav-nav2 .nav-link {
  color: white;
}

.nav-nav2 .nav-link:hover {
  color: rgb(73, 152, 241);
}

.nav-nav1 .nav-link:hover {
  color: rgb(73, 152, 241);
}

.link1 {
  text-decoration: none;
  color: black;
}

.link1-scroll {
  color: black;
}
.link1-noScroll {
  color: black;
}

.nav-links1 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.links {
  list-style: none;
  width: 33%;
}
.link {
  display: block;
  padding: 16px 20px;
  text-decoration: none;
  color: black;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  letter-spacing: 4px;
  text-align: center;
}

.link-scroll {
  color: black;
}

.link-noScroll {
  color: black;
}

.link:hover {
  color: rgb(114, 196, 216);
}

.nav-c {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
  background: chartreuse;
}
@media screen and (max-width: 930px) {
  .navbar {
    width: 100%;
    justify-content: flex-end;
  }
  .navbar-nav {
    display: none;
  }
}
