* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.hero {
  width: 100vw;
  height: 90vh;
  display: flex;
  background-image: url("./bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  position: relative;
}
.dc,
.moto {
  position: absolute;
  color: white;
}

.dc {
  top: 25vh;
  left: 10vh;
  font-size: 100px;
  font-family: Roboto;
  font-weight: bold;
  letter-spacing: 10px;
  color: aquamarine;
}

.moto {
  top: 40vh;
  left: 45vh;
  font-size: 50px;
  font-family: Montserrat;
  letter-spacing: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  word-wrap: break-word;
  text-align: center;
}

.mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 90vh;
  background: rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 990px) {
  .dc {
    font-size: 45px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
  }
  .moto {
    font-size: 30px;
    left: 50%;
    transform: translateX(-50%);
  }
}
