* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.cont {
  width: 100vw;
  min-height: 100vh;
  overflow-y: auto;

  background-image: url("./contact.png");
  background-repeat: repeat;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
}

.c-mask {
  position: absolute;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
}
.cont-c {
  width: 50%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 10;
}

.con-title {
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  font-family: "Roboto", sans-serif;
  color: white;
}

.contact-form {
  width: 100%;
  display: flex;
  padding: 25px 5px;
  align-items: flex-start;
  justify-content: center;
  border-radius: 6px;
  flex-direction: column;
}

.em,
.nam,
.mess {
  width: 100%;
  display: flex;
  margin-bottom: 20px;
  margin-left: 20px;
  align-items: center;
  justify-content: flex-start;
}

.f-name,
.l-name {
  width: 43.5%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.l-name {
  margin-left: 20px;
}
.fir {
  width: 100%;
  height: 50px;
}
.las {
  width: 100%;
  height: 50px;
}
.ema {
  width: 90%;
  height: 50px;
}
.las,
.fir,
.ema,
.me {
  padding-left: 20px;
  font-size: 20px;
  font-family: "Roboto", sans-serif;
  border: 1px solid #063d4e;
  outline: none;
  background: rgba(231, 213, 106, 0.6);
  color: black;
}
.c-title {
  width: 100%;
  height: 8vh;
  color: black;
  font-family: Montserrat;
  font-size: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 4px;
}

.me {
  padding-top: 20px;
  resize: none;
}

.cont-btn {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.send-btn {
  width: 180px;
  height: 90%;
  display: flex;
  outline: none;
  border: none;
  justify-content: center;
  align-items: center;
  background: rgb(231, 213, 106);
  font-family: Roboto;
  letter-spacing: 2px;
}
.send-btn:hover {
  cursor: pointer;
}
.te,
.fon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Roboto", sans-serif;
  color: black;
  flex: 1;
  font-size: 30px;
}

.em,
.mess {
  width: 100%;
}

.me {
  width: 100%;
}

.nam {
  width: 100%;
}

.ema {
  width: 100%;
}

.f-name,
.l-name {
  width: 50%;
}

.fir,
.las {
  width: 100%;
}

@media screen and (max-width: 900px) {
  .contact-form {
    width: 90%;
  }
  .cont-c {
    width: 100%;
  }
  .contact-form {
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nam {
    width: 90%;
  }

  .ema {
    width: 90%;
  }

  .nam,
  .em,
  .mess {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .f-name,
  .l-name {
    width: 50%;
  }

  .me {
    width: 90%;
  }
}
