body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  position: relative;
}

.header {
  width: 100vw;
  min-height: 100px;
  overflow-y: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0px 30px;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
  color: black;
  flex-direction: column;
}

.head1 {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.head2 {
  width: 100%;
  background: rgba(30, 158, 25, 0.6);
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.header-scroll {
  position: fixed;
  top: 0;
  left: 0;
  background: rgb(255, 255, 255);
}

.header-noScroll {
  position: relative;
}

.logo {
  height: 100px;
  width: 100px;
  font-size: 40px;
  font-family: Lobster;
  color: white;
}

.logo1 {
  height: 100px;
  width: 100px;
  font-size: 40px;
  font-family: Lobster;
  color: white;
  z-index: -10;
}

.logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo1 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.soc {
  width: 10%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.so i {
  font-size: 1.5rem;
  margin: 0px 10px;
  color: antiquewhite;
  color: rgb(73, 152, 241);
}

.so i:hover {
  cursor: pointer;
}

.test {
  transform: translateY(100%);
}

.line {
  width: 25px;
  height: 4px;
  background: black;
  margin-bottom: 3px;
}

.line1-animate {
  transform: rotate(45deg) translateX(5px);
  transition: transform 0.4s ease-in-out;
  background: rgb(168, 53, 53);
}
.line3-animate {
  transform: rotate(-45deg) translateX(5px);
  transition: transform 0.4s ease-in-out;
  background: rgb(168, 53, 53);
}
.line2-animate {
  display: none;
}
.line1-noAnimate {
  transform: rotate(0) translateX(0);
  transition: transform 0.4s ease-in-out;
}
.line3-noAnimate {
  transform: rotate(0) translateX(0);
  transition: transform 0.4s ease-in-out;
}
.line2-noAnimate {
  display: flex;
  transition: transform 0.4s ease-in-out;
}

.hamburger {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-right: 40px;
  display: none;
}

.hamburger:hover {
  cursor: pointer;
}

.soc1 {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 50px;
}

.soc1 .so i {
  color: white;
  font-size: 2.1rem;
}

.hidden-nav1 {
  display: none;
}

@media screen and (max-width: 1000px) {
  .soc {
    display: none;
  }
}

@media screen and (max-width: 930px) {
  .hamburger {
    display: flex;
  }
  .navbar-nav {
    display: none;
  }

  .hidden-nav1 {
    width: 100vw;
  }

  .hidden-navv {
    width: 100%;
  }

  .links {
    width: 100vw;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }

  .link {
    width: 100%;
    display: block;
    padding: 16px 12px;
  }
  .hidden-nav1 {
    display: flex;
    flex-direction: column;
  }
}
