* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.team {
  width: 100vw;
  min-height: 100vh;
  overflow-y: auto;
  background-image: url("./team.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.members {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.member {
  width: 90%;
  min-height: 200px;
  display: flex;
  border: 1px solid black;
  margin-bottom: 20px;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px 0px 10px 0px;
  color: white;
}

.m-image {
  width: 300px;
  height: 300px;
  margin-right: 30px;
  border-radius: 50%;
  margin-left: 30px;
  box-shadow: 0px 8px 8px #6c63ff;
}

.m-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.m-info {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.m-info1 {
  margin-left: 30px;
  text-align: right;
}

.m-name {
  font-size: 40px;
  font-family: Montserrat;
  letter-spacing: 5px;
  font-weight: bold;
  height: 80px;
  width: 100%;
}

.m-d {
  width: 100%;
  font-family: Roboto;
  font-size: 20px;
  letter-spacing: 2px;
  min-height: 50px;
  overflow-y: auto;
}

@media screen and (max-width: 1000px) {
  .member {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .m-info,
  .m-info1,
  .m-name,
  .m-d {
    text-align: center;
    margin-bottom: 10px;
  }

  .m-name {
    margin-bottom: 20px;
  }
  .m-image {
    margin-bottom: 20px;
  }
}
